import React, { Component } from 'react';

import { BrowserRouter as Router, Route, Link } from 'react-router-dom';

import { OfflineWorldScreen } from './offline-world-screen';
import { BreathScreen } from './breath-screen';
import { HomeScreen } from './home-screen';

import './App.css';

import styled, { injectGlobal } from 'styled-components';
import styledNormalize from 'styled-normalize';

import { rem, media } from './utils';

import { styles } from './shared'

// prepending __ means styled component
const __theme = styled.div`
  background: ${props => (props.IS_DARK ? 'black' : 'white')};

  a {
    color: ${props => (props.IS_DARK ? 'white' : 'black')};
  }
`;

const __toggle = styled.div`
  position: absolute;
  top: 144px;
  right: 144px;

  cursor: pointer;
`;

export default class App extends Component {
  constructor(props) {
    super(props);

    this.state = { isDark: false };
  }

  toggleTheme() {
    this.setState({ isDark: !this.state.isDark });
  }

  render() {
    const RenderApp = () => {
      return (
        <Router>
          <__theme IS_DARK={this.state.isDark} className="Section">
            <div className="Nav">
              <Link to="/"></Link>
              <Link to="/box-breath"></Link>
              <Link to="/offline-world"></Link>
            </div>

            <Route exact path="/" component={HomeScreen} />
            <Route exact path="/box-breath" component={BreathScreen} />
            <Route exact path="/offline-world" component={OfflineWorldScreen} />

            <__toggle onClick={this.toggleTheme.bind(this)}>
              <svg
                width="22"
                height="22"
                viewBox="0 0 46 46"
                xmlns="http://www.w3.org/2000/svg"
                xmlnsXlink="http://www.w3.org/1999/xlink"
              >
                <g fill="none" fillRule="evenodd">
                  <path fill="#FECB2F" d="M23 0a23 23 0 0 1 0 47V0z" />
                  <path
                    fill="#2B2A2A"
                    d="M23 0v47C10 47 0 36 0 23 0 11 10 0 23 0z"
                  />
                </g>
              </svg>
            </__toggle>
          </__theme>
        </Router>
      );
    };

    return <RenderApp />;
  }
}

const normalize = injectGlobal`
  ${styledNormalize}
`;

const globalStyles = injectGlobal`
  body {
    transition: background-color .6s ease;
    background-color: #222;
    color: white;
  }
  .links {
    display: none;

    &:last-child {
      ${rem('margin-bottom', 112)};

      ${media.largeDesktop`
        margin-bottom: 0;
      `}
    }
  }
`;



