import styled, { css } from 'styled-components'

import { getRandomInt } from '../utils'

// returns a random color from the palette
export const getRandomColor = () => {
  let randomInt = getRandomInt(0, palette.length)
  return `${palette[randomInt]}`
}

export const palette = [
  '#000000',
  '#081E3C',
  '#2B2A2A',
  '#3781B3',
  '#3CBEB5',
  '#4A4A4A',
  '#777777',
  '#87C46B',
  '#979797',
  '#CCCCCC',
  '#E8F0FB',
  '#FECB2F',
  '#FFFFFF',
]

export const __centered = styled.div`
  & > svg {
    position: absolute;

    margin: auto;

    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
  }
`


