import React, { Component } from 'react';

import { Breath } from './Breath';
import { Modal } from '../shared/Modal';

// import { Timer } from './Timer'

import styled from 'styled-components';

// prepending __ means styled component
const __modal = styled.div`
  visibility: ${props => (props.isVisible ? 'visible' : 'hidden')};
`;

export class BreathScreen extends Component {
  constructor(props) {
    super(props);

    let __DEV__ = process.env.NODE_ENV === 'development' ? true : false;

    __DEV__
      ? (this.state = { isInstructions: false })
      : (this.state = { isInstructions: true });
  }

  handleClick() {
    console.log(this.state.isInstructions);
    this.setState({ isInstructions: !this.state.isInstructions });
  }

  render() {
    return (
      <div onClick={this.handleClick.bind(this)}>
        <__modal isVisible={this.state.isInstructions}>
          <Modal />
        </__modal>
        <Breath />
      </div>
    );
  }
}



