import React, { Component } from 'react';

import './Modal.css';

export class Modal extends Component {
  render() {
    return (
      <div className="Modal">
        <div className="textbox">
          <div>
            <h3>What is box breathing?</h3>
            <p>
              Box breathing is a technique used to calm yourself down with a simple 4 second
              rotation of breathing in, holding your breath, breathing out, holding your breath, and
              repeating.
            </p>
          </div>
          <h3>Instructions</h3>
          <div>
            <ol>
              <li>Inhale for 4 seconds (as the circle expands)</li>
              <li>
                Hold your lungs full for 4 seconds (as the circle stays fully expanded).
              </li>
              <li>Exhale for 4 seconds (as the circle shrinks) </li>
              <li>
                Hold your lungs empty for 4 seconds (as the circle is contracted)
              </li>
            </ol>
          </div>
        </div>
      </div>
    );
  }
}



